'use strict';

// MODULES //
var isnan = __var_0__;
var pow = __var_1__;
var SQRT_PI = __var_2__;
var PI = __var_3__;


// VARIABLES //

var SKEWNESS = 2.0 * SQRT_PI * ( PI-3.0 ) / pow( 4.0-PI, 1.5 );


// MAIN //

/**
* Returns the skewness of a Rayleigh distribution.
*
* @param {NonNegativeNumber} sigma - scale parameter
* @returns {NonNegativeNumber} skewness
*
* @example
* var v = skewness( 9.0 );
* // returns ~0.631
*
* @example
* var v = skewness( 2.0 );
* // returns ~0.631
*
* @example
* var v = skewness( -0.2 );
* // returns NaN
*
* @example
* var v = skewness( NaN );
* // returns NaN
*/
function skewness( sigma ) {
	if ( isnan( sigma ) || sigma < 0.0 ) {
		return NaN;
	}
	return SKEWNESS;
}


// EXPORTS //
export default skewness;
